.appLanguage {
  transition-duration: .3s;
  &--active, &:hover {
    background: rgba(67, 71, 83, 0.4);
    transition-duration: .3s;
  }
  &--auth {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    padding: 20px 20px;
    cursor:pointer;
    img {
      margin-right: 10px;
    }
  }
}
.appLanguageDropdown {
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.4);
  width: 100%;
  height: 100vh;
  z-index: 10;
  .content{
    position: fixed;
    left: 0;
    bottom: 0;
    background: #2C2F38;
    width: 280px;
    box-shadow: 0px 8px 10px rgba(12, 13, 15, 0.2), 0px 6px 30px rgba(12, 13, 15, 0.12), 0px 16px 24px rgba(12, 13, 15, 0.14);
    border-top-right-radius: 8px;
    padding: 10px 0;
    div {
      padding: 10px 20px;
      background: rgba(255,255,255,0);
      transition-duration: .3s;
      display: flex;
      flex-direction:row;
      align-items:middle;
      border-left:3px solid transparent;
      cursor:pointer;
      img {
        margin-right: 15px;
      }
      &:hover {
        background: rgba(255,255,255,0.08);
        transition-duration: .3s;
        border-left:3px solid #6279C3;
      }
      &.currentLang {
        background: rgba(255,255,255,0.08);
        border-left:3px solid #6279C3;
      }
    }
  }
}